@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html, body {
  min-height: 100%;
  font-family: "Montserrat", sans-serif; }

.app {
  height: 100vh;
  background-image: url(/weather-app/static/media/background.8c1eca03.jpg);
  background-size: cover;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr); }

.header {
  grid-column: 1 / -1;
  grid-row: 1 / 2;
  display: flex;
  justify-content: space-evenly;
  align-items: center; }
  .header .search-bar {
    display: flex; }
  .header .weather-input {
    text-align: center;
    font-size: 2rem;
    background: rgba(220, 220, 220, 0.5);
    border-radius: 10rem;
    outline: none;
    border: none; }
  .header .submit-button {
    background-color: transparent;
    border: none;
    font-size: 1rem;
    margin-left: .5rem;
    display: none; }
  .header .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px; }
  .header .switch input {
    opacity: 0;
    width: 0;
    height: 0; }
  .header .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s; }
  .header .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s; }
  .header input:checked + .slider {
    background-color: #2196F3; }
  .header input:focus + .slider {
    box-shadow: 0 0 1px #2196F3; }
  .header input:checked + .slider:before {
    transform: translateX(26px); }
  .header .slider.round {
    border-radius: 34px; }
  .header .slider.round:before {
    border-radius: 50%; }

.main-content {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background: rgba(220, 220, 220, 0.5);
  font-size: 1.5rem;
  border-radius: 10px; }
  .main-content .location {
    font-size: 2rem; }
  .main-content .temp {
    font-size: 4rem; }

.footer {
  grid-column: 1 / -1;
  grid-row: 3 / -1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 5rem;
  background: rgba(220, 220, 220, 0.5); }
  .footer .info {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .footer .info * {
      margin: .5rem; }

